

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import router from './router'
import './lib/js/rem' 

import store from './store'
import * as types from './store/mutation'

import {sync} from 'vuex-router-sync'
import animate from 'animate.css'
import Navigation from 'vue-navigation'



/**
* vuex-router-sync vux
* When the router navigates to a new route, the store's state is updated.
* store.state.route.path   // current path (string)
* store.state.route.params // current params (object)
* store.state.route.query  // current query (object)
* */
import api from './constant/api'
import App from './App'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import CloseDialogsPlugin from 'vux/src/plugins/close-dialogs/index.js'
import ConfigPlugin from 'vux/src/plugins/config/index.js'
import BusPlugin from 'vux/src/plugins/bus/index.js'
import DevicePlugin from 'vux/src/plugins/device/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import WechatPlugin from 'vux/src/plugins/wechat/index.js'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'


import VueSocketio from 'vue-socket.io'
Vue.use(VueSocketio, 'http://198.211.33.137:3000')
console.log('socket:' + VueSocketio)
Vue.use(VueRouter)
Vue.use(animate)
Vue.use(Navigation, {router, store, moduleName: 'navigation', keyName: 'VPK'}) 

Vue.use(ConfigPlugin, {
$layout: 'VIEW_BOX' 
})

Vue.use(api)
Vue.use(DevicePlugin)
Vue.use(ToastPlugin)
Vue.use(AlertPlugin)
Vue.use(ConfirmPlugin)
Vue.use(LoadingPlugin)
Vue.use(WechatPlugin)
Vue.use(AjaxPlugin)
Vue.use(BusPlugin)
Vue.use(DatetimePlugin)
Vue.use(CloseDialogsPlugin, router)


sync(store, router) 
FastClick.attach(document.body)
FastClick.attach(document.body)
Vue.config.productionTip = false

/* http config */

Vue.http.defaults.timeout = 50000
Vue.http.defaults.baseURL = 'http://198.211.33.137:3000'






Vue.prototype.$http.interceptors.request.use(
config => {
if (store.state.login.token) {

config.headers.common['x-broncos-token'] = store.state.login.token

}
return config
},
err => {
return Promise.reject(err)
})

Vue.prototype.$http.interceptors.response.use(
response => {
console.log('eee:' + response)

if (response.data.code === 401) {

store.commit(types.LOGOUT)
router.replace({
path: '/login',
query: {redirect: router.currentRoute.fullPath}
})
} else {
return response.data  
}
},
error => {
alert('Sorry, api error, please contact wechat: zdjhappy, ' + error)
if (error.response.data) {
switch (error.response.status) {
case 401:

store.commit(types.LOGOUT)
router.replace({
path: '/login',
query: {redirect: router.currentRoute.fullPath}
})
}
}

return Promise.reject(error.response.data)
})

/**
* 
*/
function toEmotion (text, isNoGif) {
let list = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', 'NO', 'OK', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']

if (!text) {
return text
}

text = text.replace(/\[[\u4E00-\u9FA5]{1,3}\]/gi, function (word) {
let newWord = word.replace(/\[|\]/gi, '')
let index = list.indexOf(newWord)
let backgroundPositionX = -index * 24
let imgHTML = ''
if (index < 0) {
return word
}

if (isNoGif) {
if (index > 104) {
return word
}
imgHTML = `<i class="static-emotion" style="background:url(https://res.wx.qq.com/mpres/htmledition/images/icon/emotion/default218877.gif) ${backgroundPositionX}px 0;"></i>`
} else {
let path = index > 104 ? '/img' : 'https://res.wx.qq.com/mpres/htmledition/images/icon'
imgHTML = `<img class="static-emotion-gif" style="vertical-align: middle" src="${path}/emotion/${index}.gif">`
}
return imgHTML
})
return text
}


Vue.directive('emotion', {
bind: function (el, binding) {
el.innerHTML = toEmotion(binding.value)
}
})

/**
* 
*/
Vue.config.productionTip = false
/* eslint-disable no-new */
new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')



